@font-face {
  font-family: 'icomoon';
  src:  url('#{$icons}/icomoon.eot?a78kxm');
  src:  url('#{$icons}/icomoon.eot?a78kxm#iefix') format('embedded-opentype'),
    url('#{$icons}/icomoon.ttf?a78kxm') format('truetype'),
    url('#{$icons}/icomoon.woff?a78kxm') format('woff'),
    url('#{$icons}/icomoon.svg?a78kxm#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

